import React from 'react';

// (3-1) /src       
// -->      
// -->  react    .
import sample from '../../assets/img/sample.png';

/**
 * Inline Css  
 * ex) <div style="..."></div>
 */
const InlineCss = () => {
    /** (1-1) CSS  JSON   */
    // CSS     .
    // ex) document.getElementById("hello").style.backgroundColor = "#ff00ff";
    const myStyle = {
        backgroundColor: '#f60',
        fontSize: '20px',
        color: '#0f6',
        fontWeight: 'bold',
        padding: '10px 25px',
        marginBottom: '10px'
    };

    return (
        <div>
            <h2>InlineCss</h2>

            <h3>  CSS </h3>
            {/* (1-2) JSON style   */}
            <div style={myStyle}>Hello React Css (1)</div>


            <h3> CSS </h3>
            {/* (2) CSS   */}
            <div style={{
                backgroundColor: '#ff0',
                fontSize: '20px',
                color: '#00f',
                fontWeight: 'bold',
                padding: '10px 25px',
                marginBottom: '10px'
            }}>Hello React Css (2)</div>

            <h3> </h3>
            {/* (3-2)   alt      */}
            <img src={sample} width='240' height='240' alt='' />

            {/* (3-3) public       
                (public     ) */}
            <img src={`${process.env.PUBLIC_URL}/logo192.png`} width='240' height='240' alt='react' />
        </div>
    );
};

export default InlineCss;